@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@600;800&family=Tajawal:wght@300;500;700&family=Dosis:wght@200;500;600&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
:root {
  --default-color: #1a1a2e;
  --transparent-default-color: rgba(26, 26, 46, 0.7);
  --transparent-dark-color: rgba(26, 26, 46, 0.9);
}

* {
  margin: 0;
  padding: 0;

  box-sizing: border-box;
  overflow-x: hidden;

  color: #1a1a2e;

  color: var(--default-color);
}

html {
  scroll-behavior: smooth;
}

input,
button {
  border: none;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  outline: none;
}

/*
* Fonts used:
* - Exo 2
* - Material Icons
* - Dosis
*/

.page-container {
  position: relative;
}

.canvas-container {
  background-color: #1a1a2e;

  min-width: 100vw;
  min-height: 100vh;
  overflow: hidden;

  display: grid;
  grid-template-columns: 15vw 1fr;
}

.image-container {
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 40px 0;
}

.image-container .image {
  position: relative;

  display: flex;
}

.image-container .image img {
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.selection {
  position: absolute;

  width: 100px;
  height: 100px;

  border: 4px dashed #1a1a2e;
  border-radius: 50%;

  opacity: 1;
  transition: 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.selection.hidden {
  opacity: 0;
}

.selection.static {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;

  border-color: white;
  transition: 200ms;
}

.selection.static:hover {
  background-color: var(--transparent-default-color);
}

.selection.static p {
  color: white;
  font-weight: bold;
  font-family: "Tajawal", sans-serif;

  opacity: 0;
  transition: 200ms;
}

.selection.static:hover p {
  opacity: 1;
}

.selection.static.hidden {
  display: none;
}

.sidenav {
  width: 100%;
  height: 100%;

  background-color: white;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidenav .sidenav-content {
  position: fixed;
  margin: 0;
  width: 15vw;
}

.sidenav header {
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  margin: 20px 0 30px;
}

.sidenav header h1 {
  font-family: "Exo 2", sans-serif;
  font-weight: 500;
  font-size: 34px;

  line-height: 1.5em;
}

.sidenav header h1 strong {
  font-weight: 500;
  color: orangered;
}

.sidenav section {
  font-family: "Tajawal", sans-serif;
  padding: 0 20px;
}

.sidenav section h2 {
  font-weight: 800;
  font-size: 22px;

  margin-bottom: 10px;
}

.sidenav section .character {
  padding: 0 8px;
}

.sidenav section .character:hover {
  background-color: orangered;
  cursor: pointer;
}

.sidenav section .character p {
  font-weight: 500;
  font-size: 18px;
}

.sidenav section .character:hover p {
  color: white;
}

.sidenav section .character span {
  font-weight: 300;
  font-size: 12px;
}

.sidenav section .character:hover span {
  color: white;
}

.sidenav section .character + .character {
  margin-top: 10px;
}

.sidenav hr {
  margin: 20px 40px;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;

  display: flex;
  justify-content: center;

  width: 100%;
  height: 100%;

  background-color: var(--transparent-dark-color);
}

.overlay .overlay-content {
  height: 100vh;
  width: 400px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.endScreen-container {
  padding: 0 20px;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.endScreen-container h1 {
  color: white;

  width: 100%;
  padding-top: 12px;
  margin-bottom: 80px;
  border: 4px solid white;
  border-radius: 10px;
  text-align: center;

  font-family: "Tajawal", sans-serif;
  font-size: 40px;
}

.endScreen-container h2 {
  color: white;

  margin: 15px 0;
  line-height: 1.3em;

  font-family: "Dosis", sans-serif;
  font-size: 32px;
  font-weight: 800;
}

.endScreen-container p {
  color: white;

  font-family: "Tajawal", sans-serif;
}

.endScreen-container form {
  display: grid;
  grid-template-columns: 4fr 80px;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 50px;
  margin: 50px 0 20px;

  padding-left: 8px;

  background-color: var(--default-color);
  border-radius: 10px;
  box-shadow: 0px 0px 18px -2px rgba(250, 250, 250, 0.3);

  font-family: "Tajawal", sans-serif;
}

.endScreen-container form input {
  background-color: var(--default-color);
  padding-top: 8px;

  font-size: 24px;
  height: 100%;
  color: white;
}

.endScreen-container form button {
  background-color: white;
  padding-top: 8px;

  font-size: 20px;
  height: 100%;
  color: var(--default-color);
}

.highscores-container {
  width: 100%;
  padding: 0 20px;
}

.highscores-container h1 {
  color: white;

  width: 100%;
  padding-top: 12px;
  margin-bottom: 40px;
  border: 4px solid white;
  border-radius: 10px;
  text-align: center;

  font-family: "Tajawal", sans-serif;
  font-size: 40px;
}

.highscores-container .highscores {
  width: 100%;
  border: 2px solid white;
  border-radius: 8px;

  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 8px;
  gap: 8px;

  padding: 20px 15px;
}

.highscores-container .highscores li {
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;

  color: white;
  font-size: 20px;
  font-family: "Dosis", sans-serif;
  line-height: 1.5em;

  border-radius: 10px;
  padding: 0 8px;
}

.highscores-container .highscores li span {
  color: #fd8c21;

  font-weight: 500;
}

.highscores-container .highscores li.podium {
  font-weight: bold;
}

.highscores-container .highscores li.podium span {
  color: white;
  font-weight: bold;
}

.highscores-container .highscores li.first {
  position: relative;

  justify-content: flex-start;

  background-color: #fda403;
}

.highscores-container .highscores li.first span {
  position: absolute;
  right: 8px;
}

.highscores-container .highscores li.first:before {
  display: block;
  content: " ";
  background-image: url(/where-is-waldo/static/media/trophy.3eb22b96.svg);
  background-size: 20px 20px;
  height: 20px;
  width: 20px;

  margin-right: 8px;
}

.highscores-container .highscores li.second {
  background-color: #c0c0c0;
}

.highscores-container .highscores li.third {
  background-color: #d65f00;
}

